import React from 'react'
import PageLayout from 'layouts/PageLayout'
import Filter from 'app/Filter'
import TableHeader from 'app/table/TableHeader'
import SortOrder from 'services/SortOrder'

const onClickHandler = (id, sort) => {
  console.log(`clicked: ${id}, ${sort}`)
}

const payload = {
  columns: [
    {
      id: 0,
      label: 'Alert Type',
      isSortable: true,
      sortOrder: SortOrder.ASC,
      onClick: onClickHandler,
    },
    {
      id: 1,
      label: 'Title',
      isSortable: true,
      onClick: onClickHandler,
    },
    {
      id: 2,
      label: 'Description',
      isSortable: false,
      sortOrder: SortOrder.NONE,
      onClick: onClickHandler,
    },
    {
      id: 3,
      label: 'Periodicity',
      isSortable: true,
      sortOrder: SortOrder.NONE,
      onClick: onClickHandler,
    },
    {
      id: 4,
      label: 'Time',
      isSortable: true,
      sortOrder: SortOrder.NONE,
      onClick: onClickHandler,
    },
    {
      id: 6,
      label: 'Assigned to',
      isSortable: true,
      sortOrder: SortOrder.NONE,
      onClick: onClickHandler,
    },
  ],
}

const grid = '3fr 2fr minmax(200px, 4fr) 1fr 1fr 3fr'

const Component = () => {
  return (
    <PageLayout
      header={
        <Filter
          actionName='Add Alert'
          placeholder='Enter Location'
          title='Alerts'
        />
      }>
      <TableHeader model={payload.columns} gridTemplateColumns={grid} />
      Alerts!!!
    </PageLayout>
  )
}

export default Component
