import React, { useState } from 'react'
import PropTypes from 'prop-types'
import { FormUp, FormDown } from 'grommet-icons'
import SortOrder from 'services/SortOrder'
import { Box } from 'grommet'
import colors from 'styles/colors'

const styles = {
  wrapper: {},
  main: {
    userSelect: 'none',
  },
  chevron: {
    width: 16,
  },
  label: {
    letterSpacing: -0.24,
  },
}

const TableHeaderItem = ({
  id,
  label,
  sortOrder,
  isSortable,
  onClick,
}) => {
  const [sort, setSort] = useState(sortOrder)

  const onCLickHandler = () => {
    if (isSortable) {
      let ss = SortOrder.NONE
      if (sort === SortOrder.NONE) {
        ss = SortOrder.ASC
      } else if (sort === SortOrder.ASC) {
        ss = SortOrder.DESC
      } else {
        ss = SortOrder.ASC
      }
      onClick(id, ss)
      setSort(ss)
    }
  }

  let chevron = null
  if (isSortable) {
    if (sort === SortOrder.NONE) {
      chevron = null
    } else if (sort === SortOrder.ASC) {
      chevron = <FormDown color={colors.red} style={styles.chevron} />
    } else {
      chevron = <FormUp color={colors.red} style={styles.chevron} />
    }
  }


  return (
    <span>
      <Box
        direction='row'
        style={styles.main}
        onClick={onCLickHandler}>
        <span className='bold f-s-14' style={styles.label}>
          {label}
        </span>
        {chevron}
      </Box>
    </span>
  )
}

TableHeaderItem.propTypes = {
  id: PropTypes.number,
  sortOrder: PropTypes.number,
  isSortable: PropTypes.bool,
  onClick: PropTypes.func,
  width: PropTypes.string,
}

TableHeaderItem.defaultProps = {
  id: 0,
  onClick: () => {},
  isSortable: true,
  sortOrder: SortOrder.NONE,
  width: 'auto',
}

export default TableHeaderItem
