import React from 'react'
import { withRouter } from 'react-router-dom'
import {
  Switch,
  Route,
  Redirect,
  BrowserRouter as Router,
} from 'react-router-dom'
import { Grommet } from 'grommet'
import routes from 'services/routes'

import theme from './app/theme'
import PrivateRoute from './app/PrivateRoute'
import NotFound from './pages/NotFoundPage'
import LoginPage from './pages/LoginPage'
import Home from './pages/HomePage'
import BaseLayout from 'layouts/BaseLayout'
import { useStoreActions } from 'easy-peasy'
import Alerts from 'pages/AlertsPage'
import Contacts from 'pages/ContactsPage'
import Documents from 'pages/DocumentsPage'
import Equipment from 'pages/EquipmentPage'
import Schedule from 'pages/Schedule'

const Layout = withRouter(props => <BaseLayout {...props} />)

const App = () => {
  // temporary is Authenticated and authorized
  const authSucceed = useStoreActions(actions => {
    return actions.auth.succeed
  })

  authSucceed()

  return (
    <Grommet theme={theme}>
      <Router>
        <Layout>
          <Switch>
            <PrivateRoute exact path={routes.home}>
              <Home />
            </PrivateRoute>

            <PrivateRoute path={routes.alerts}>
              <Alerts />
            </PrivateRoute>
            <PrivateRoute path={routes.contacts}>
              <Contacts />
            </PrivateRoute>
            <PrivateRoute path={routes.documents}>
              <Documents />
            </PrivateRoute>
            <PrivateRoute path={routes.equipment}>
              <Equipment />
            </PrivateRoute>
            <PrivateRoute path={routes.schedule}>
              <Schedule />
            </PrivateRoute>

            <Route path={routes.login}>
              <LoginPage />
            </Route>
            <Route path={routes.notFound}>
              <NotFound />
            </Route>
            <Redirect to={routes.notFound}>
              <NotFound />
            </Redirect>
          </Switch>
        </Layout>
      </Router>
    </Grommet>
  )
}

export default App
