import React, { useState } from 'react'
import PageLayout from 'layouts/PageLayout'
import Filter from 'app/Filter'
import TableHeader from 'app/table/TableHeader'
import SortOrder from 'services/SortOrder'

const quickActions = [
  {
    id: 0,
    name: 'All',
    isActive: true,
  },
  {
    id: 1,
    name: 'Free',
    isActive: false,
  },
  {
    id: 2,
    name: 'Busy',
    isActive: false,
  },
  {
    id: 3,
    name: 'Under Repair',
    isActive: false,
  },
]

const onClickHandler = (id, sort) => {
  console.log(`clicked: ${id}, ${sort}`)
}

const payload = {
  columns: [
    {
      id: 0,
      label: 'Model',
      isSortable: true,
      sortOrder: SortOrder.ASC,
      onClick: onClickHandler,
    },
    {
      id: 1,
      label: 'Type',
      isSortable: true,
      onClick: onClickHandler,
    },
    {
      id: 2,
      label: 'Location',
      isSortable: false,
      sortOrder: SortOrder.NONE,
      onClick: onClickHandler,
    },
    {
      id: 3,
      label: 'Assigned to',
      isSortable: true,
      sortOrder: SortOrder.NONE,
      onClick: onClickHandler,
    },
    {
      id: 4,
      label: 'Status',
      isSortable: true,
      sortOrder: SortOrder.NONE,
      onClick: onClickHandler,
    },
  ],
}

const grid = '2fr 1fr minmax(200px, 3fr) 2fr 2fr'

const Component = () => {
  const [actions, setActions] = useState(quickActions)

  const selectActionHandler = id => {
    const a = [...actions].map(action => {
      action.isActive = action.id === id
      return action
    })
    setActions(a)
  }

  return (
    <PageLayout
      header={
        <Filter
          actionName='Add Equipment'
          placeholder='Enter Name, Location, Type...'
          quickAction={actions}
          onSelectAction={selectActionHandler}
          title='Equipment'
        />
      }>
      <TableHeader
        model={payload.columns}
        gridTemplateColumns={grid}
      />
      Equipment!!!
    </PageLayout>
  )
}

export default Component
