import React, { useState } from 'react'
import PropTypes from 'prop-types'
import useWindowDimension from 'services/useWindowDimension'
import colors from 'styles/colors'
import MoreIcon from './tableRow/MoreIcon'

const styles = {
  main: {
    display: 'flex',
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'space-between',
    backgroundColor: colors.white,
    maxHeight: 80,
    borderRadius: 24,
  },
  children: {
    display: 'grid',
    gap: '40px',
    alignItems: 'center',
    width: '100%',
  },
  more: {
    cursor: 'pointer',
    userSelect: 'none',
    marginTop: 7,
    position: 'relative',
  },
  menu: {
    zIndex: 700,
    position: 'absolute',
    width: 217,
    padding: '31px 27px 24px 27px',
    right: 0,
    backgroundColor: colors.white,
    borderRadius: 12,
    border: `0.3px solid ${colors.greyLight}`,
  },
  menuItem: {
    lineHeight: '3em',
  },
}

const TableRow = ({
  children,
  id,
  gridTemplateColumns,
  menuItems,
  showMenu,
  rowHeight,
  menuItemSelected, // when Menu Item is chosen
  iconMoreClicked, // when More icon clicked
}) => {
  const [menuStyle, setMenuStyle] = useState(styles.menu)
  const { height } = useWindowDimension()

  const childrenStyle = {
    ...styles.children,
    gridTemplateColumns: gridTemplateColumns,
  }

  const handleMoreClick = e => {
    console.log('more click')

    e.stopPropagation()
    setMenuStyle({
      ...menuStyle,
      bottom: e.clientY / height > 0.7 ? '35px' : 'none',
    })

    iconMoreClicked(id)
  }

  const handleMenuItemClick = itm => {
    console.log('menu click', itm)
    menuItemSelected(itm)
  }

  const mainStyle = { ...styles.main, maxHeight: `${rowHeight}px` }

  return (
    <div style={mainStyle} className='p-t-25 p-r-43 p-b-25 p-l-27'>
      <div style={childrenStyle}>{children}</div>

      <div style={styles.more}>
        <MoreIcon
          stroke={showMenu ? colors.red : colors.greyDark}
          onClick={handleMoreClick}
        />

        {showMenu && menuItems && (
          <div style={menuStyle}>
            {menuItems.map(itm => {
              return (
                <div
                  className='bold f-s-16'
                  style={styles.menuItem}
                  key={itm.id}
                  onClick={() => handleMenuItemClick(itm)}>
                  {itm.name}
                </div>
              )
            })}
          </div>
        )}
      </div>
    </div>
  )
}

TableRow.propTypes = {
  children: PropTypes.node,
  id: PropTypes.number,
  gridTemplateColumns: PropTypes.string.isRequired,
  menuItems: PropTypes.array,
  showMenu: PropTypes.bool,
  rowHeight: PropTypes.number,
  menuItemSelected: PropTypes.func,
  iconMoreClicked: PropTypes.func,
}

TableRow.defaultProps = {
  menuShows: false,
  rowHeight: 80,
  menuItemSelected: () => {},
  iconMoreClicked: () => {},
}

export default TableRow
