import React from 'react'

const style = {
  error404: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  title: {},
  goBack: {},
}

const NotFoundPage = () => (
  <>
    <div style={style.error404}>
      <div>
        <h1 className='f-s-40 m-b-20' style={style.title}>
          404: Page not found.
        </h1>
      </div>
      <div>
        <span>Go back: </span>
        <span>
          <a style={style.goBack} href='/'>
            Home Page
          </a>
        </span>
      </div>
    </div>
  </>
)

export default NotFoundPage
