import React from 'react'
import PropTypes from 'prop-types'
import colors from 'styles/colors'

const styles = {
  link: {
    borderRadius: '50%',
    width: 40,
    height: 40,
  },
  name: {
    maxHeight: 48,
    fontWeight: 500,
  },
  image: {
    minHeight: 40,
    minWidth: 40,
  },
  type: {
    color: colors.greyDark,
    fontWeight: 500,
    lineHeight: '16px',
  },
}

const Person = ({ id, link, name, position }) => {
  return (
    <div className='center-vertical' style={styles.main}>
      <div className='center' style={styles.image}>
        <img style={styles.link} src={link} alt={id} with='40' height='40' />
      </div>
      <div className='m-l-12'>
        <div className='f-s-16 text-ellipsis' style={styles.name}>
          {name}
        </div>
        <div style={styles.type}>{position}</div>
      </div>
    </div>
  )
}

Person.propTypes = {
  id: PropTypes.number.isRequired,
  name: PropTypes.string.isRequired,
  link: PropTypes.string.isRequired,
  position: PropTypes.string.isRequired,
}

export default Person
