import React from 'react'
import PropTypes from 'prop-types'
import colors from 'styles/colors'

const mixin = {
  cursor: 'pointer',
  userSelect: 'none',
}

const styles = {
  span: { ...mixin, color: colors.greyDark },
  spanActive: {
    ...mixin,
    fontWeight: 700,
    borderBottom: `3px solid ${colors.red}`,
  },
}

const QuickActions = ({ actions, onSelectAction }) => {
  return (
    <div>
      {actions && actions.map(a => {
        return (
          <span
            key={a.id}
            className='m-r-19 f-s-16 p-b-4'
            style={a.isActive ? styles.spanActive : styles.span}
            onClick={() => onSelectAction(a.id)}>
            {a.name}
          </span>
        )
      })}
    </div>
  )
}

QuickActions.propTypes = {
  actions: PropTypes.array,
  onSelectAction: PropTypes.func,
}

export default QuickActions
