import React from 'react'
import PropTypes from 'prop-types'
import { Box } from 'grommet'

import borders from 'styles/borders'
import colors from 'styles/colors'

const styles = {
  main: {
    height: 120,
    borderBottom: borders.menu,
    direction: 'column',
    alignItems: 'center',
    justifyContent: 'center',
    borderLeft: `3px solid ${colors.red}`,
    cursor: 'pointer',
    userSelect: 'none',
  },
  title: {
    color: colors.greyDark,
  },
}

const SideMenuItem = ({ name, isActive, children }) => {
  const color = !!isActive ? colors.black : colors.greyDark
  const textWeight = !!isActive ? 'bold' : ''

  styles.title = { ...styles.title, color: color }
  styles.main.borderLeft = !!isActive ? `3px solid ${colors.red}` : 'none'

  return (
    <Box style={styles.main}>
      <div>{children}</div>
      <div className={textWeight} style={styles.title}>
        {name}
      </div>
    </Box>
  )
}

SideMenuItem.propTypes = {
  children: PropTypes.node,
  name: PropTypes.string.isRequired,
  isActive: PropTypes.bool.isRequired,
}

export default SideMenuItem
