import React, { useState } from 'react'
import PageLayout from 'layouts/PageLayout'
import Filter from 'app/Filter'

const quickActions = [
  {
    id: 0,
    name: 'By Location',
    isActive: true,
  },
  {
    id: 1,
    name: 'By People',
    isActive: false,
  },
]

const Component = () => {
  const [actions, setActions] = useState(quickActions)

  const selectActionHandler = id => {
    const a = [...actions].map(action => {
      action.isActive = action.id === id
      return action
    })
    setActions(a)
  }

  return (
    <PageLayout
      header={
        <Filter
          actionName='Add Location'
          placeholder='Enter Name, Location, Type...'
          quickAction={actions}
          onSelectAction={selectActionHandler}
          title='Schedule'
        />
      }>
      Schedule!!!
    </PageLayout>
  )
}

export default Component
