import React from 'react'
import PropTypes from 'prop-types'
import { Box } from 'grommet'
import QuickActions from './filter/QuickActions'
import ZoomIcon from './filter/ZoomIcon'
import AtmButton from 'atomic/atm-button'
import AtmInput from 'atomic/atm-input'

const styles = {
  main: {
    height: '100%',
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'flex-end',
  },
  button: {
    width: 176.8,
    height: 40,
    borderRadius: 16,
    fontSize: 18,
    marginBottom: 4,
  },
  input: {
    width: 280,
    height: 40,
    marginBottom: 4,
    marginRight: 16,
    textIndent: 31,
  },
}

const Filter = ({
  title,
  quickAction,
  placeholder,
  actionName,
  onSelectAction,
}) => {
  return (
    <div style={styles.main}>
      <Box>
        <div className='f-s-40 bold'>{title}</div>
        <div className='m-t-19'>
          <QuickActions actions={quickAction} onSelectAction={onSelectAction} />
        </div>
      </Box>
      <Box direction='row'>
        <AtmInput
          id='input-filter'
          placeholder={placeholder}
          leftIcon={<ZoomIcon />}
          style={styles.input}
        />
        <AtmButton label={actionName} style={styles.button} />
      </Box>
    </div>
  )
}

Filter.propTypes = {
  title: PropTypes.string.isRequired,
  quickAction: PropTypes.array,
  placeholder: PropTypes.string.isRequired,
  actionName: PropTypes.string.isRequired,
  onSelectAction: PropTypes.func,
}

export default Filter
