import React, { useState } from 'react'
import PageLayout from 'layouts/PageLayout'
import Filter from 'app/Filter'
import Table from 'app/Table'
import SortOrder from 'services/SortOrder'

import { contactMock } from 'services/mock-data'
import range from 'core/range'
import Location from 'app/Location'
import Tool from 'app/Tool'
import Person from 'app/Person'

const quickActions = [
  {
    id: 0,
    name: 'All',
    isActive: true,
  },
  {
    id: 1,
    name: 'Send an Invite',
    isActive: false,
  },
]

const onClickHandler = (id, sort) => {
  console.log(`clicked: ${id}, ${sort}`)
}

const payload = {
  columns: [
    {
      id: 0,
      label: 'Name',
      isSortable: true,
      sortOrder: SortOrder.ASC,
      onClick: onClickHandler,
    },
    {
      id: 1,
      label: 'Current Location',
      isSortable: false,
      sortOrder: SortOrder.NONE,
      onClick: onClickHandler,
    },
    {
      id: 2,
      label: 'Tools',
      isSortable: true,
      sortOrder: SortOrder.NONE,
      onClick: onClickHandler,
    },
  ],
  items: [...range(1, 10)].map(x => {
    return { ...contactMock, id: x }
  }),
}

const grid = '2fr minmax(200px, 5fr) 3fr'

const menuItems = [
  {
    id: 0,
    name: 'Edit',
    onClick: () => {},
  },
  {
    id: 1,
    name: 'Delete',
    onClick: () => {},
  },
]

const Component = () => {
  const [actions, setActions] = useState(quickActions)

  const selectSortingHandler = id => {
    const a = [...actions].map(action => {
      action.isActive = action.id === id
      return action
    })
    setActions(a)
  }

  return (
    <PageLayout
      header={
        <Filter
          actionName='Invite Contact'
          placeholder='Enter Name, Location, Etc...'
          quickAction={actions}
          onSelectAction={selectSortingHandler}
          title='Contacts'
        />
      }>
      <Table payload={payload} grid={grid} menuItems={menuItems}>
        {itt => {
          return (
            <>
              <div>
                <Person 
                  id={itt.contact.id}
                  name={itt.contact.name}
                  position={itt.contact.position}
                  link={itt.contact.thumbnail}
                />
              </div>
              <div>
                <Location name={itt.location.name} address={itt.location.address} />
              </div>
              <div>
                <Tool id={itt.tool.id} name={itt.tool.name} type={itt.tool.type}/>
              </div>
            </>
          )
        }}
      </Table>
    </PageLayout>
  )
}

export default Component
