import React from 'react'
import PropTypes from 'prop-types'
import colors from 'styles/colors'
import borders from 'styles/borders'
import TableHeaderItem from './tableHeader/TableHeaderItem'

const styles = {
  main: {
    display: 'grid',
    gap: '40px',
    padding: '14px 107px 14px 59px',
    backgroundColor: colors.white,
    borderTop: borders.menu,
    borderBottom: borders.menu,
  },
}

const TableHeader = ({ model, gridTemplateColumns }) => {
  const headerClickHandler = (id, order) => {
    // only one thing should be sortable.
    // All sorting goes form the server
    console.log('table header click', id, order)
  }

  const mainStyle = { ...styles.main, gridTemplateColumns }

  return (
    <div style={mainStyle}>
      {model.map(item => {
        return (
          <div key={item.id}>
            <TableHeaderItem
              id={item.id}
              label={item.label}
              isSortable={item.isSortable}
              sortOrder={item.sortOrder}
              onClick={headerClickHandler}
            />
          </div>
        )
      })}
    </div>
  )
}

TableHeader.propTypes = {
  model: PropTypes.array.isRequired,
  gridTemplateColumns: PropTypes.string.isRequired,
}

export default TableHeader
