import React from 'react'
import { Box } from 'grommet'
import colors from 'styles/colors'
import AtmInput from 'atomic/atm-input'
import AtmAnchor from 'atomic/atm-anchor'
import AtmButton from 'atomic/atm-button'
// import PropTypes from 'prop-types'

const style = {
  main: {
    backgroundColor: colors.white,
    color: colors.black,
    borderRadius: 9,
    padding: '53px 56px 28px 56px',
  },
  button: {
    width: '100%',
    height: 48,
    fontSize: 18,
  },
  question: {
    color: colors.greyDark,
  },
}

const Login = () => {
  return (
    <Box direction='column' style={style.main}>
      <div className='f-s-32 m-b-30 bold'>Log In</div>
      <AtmInput
        id='email'
        type='email'
        placeholder='Email'
        className='m-b-20 center'
      />
      <AtmInput id='pwd' type='password' placeholder='Password' />
      <AtmAnchor to='/forgot-password' className='m-t-16 text-end'>
        Forgot Password?
      </AtmAnchor>
      <div className='m-t-57 bold'>
        <AtmButton type='submit' style={style.button} label='Continue' />
      </div>
      <div className='m-t-37 text-center f-s-16' style={style.question}>
        Don't have an account?
      </div>
      <AtmAnchor className='text-center m-t-6 f-s-16' to='/signup'>
        Sign Up
      </AtmAnchor>
    </Box>
  )
}

Login.propTypes = {}

export default Login
