import React from 'react'
import PropTypes from 'prop-types'

import colors from 'styles/colors'
import Header from 'app/Header'
import borders from 'styles/borders'
import SideMenu from 'app/SideMenu'
import routes from 'services/routes'

const BaseLayout = ({ children, location }) => {
  const isLoginRoute =
    location.pathname === routes.login || location.pathname === routes.notFound

  return (
    <div style={styles.main}>
      <div style={styles.menu}>
        <Header isAuth={!isLoginRoute} />
      </div>
      <div style={styles.content}>
        {!isLoginRoute && (
          <div style={styles.sideMenu}>
            <SideMenu route={location.pathname} />
          </div>
        )}
        <div style={styles.pageSpace}>{children}</div>
      </div>
    </div>
  )
}

BaseLayout.propTypes = {
  children: PropTypes.node,
  sideMenu: PropTypes.node,
}

const bShadow = '5px 4px 14px rgba(0, 0, 0, 0.04)'

const styles = {
  main: {
    zIndex: 999,
    display: 'flex',
    flexDirection: 'column',
    minHeight: '100vh',
  },
  menu: {
    display: 'flex',
    alignItems: 'center',
    background: colors.white,
    height: 80,
    boxShadow: bShadow,
    borderBottom: borders.menu,
  },
  content: {
    display: 'flex',
    flexDirection: 'rows',
  },
  pageSpace: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    background: colors.greyBg,
    height: `calc(100vh - 80px)`,
    width: '100%',
  },
  sideMenu: {
    background: colors.white,
    minWidth: 120,
    boxShadow: bShadow,
  },
}

export default BaseLayout
