import React, { useState } from 'react'
import PageLayout from 'layouts/PageLayout'
import Filter from 'app/Filter'
import TableHeader from 'app/table/TableHeader'
import SortOrder from 'services/SortOrder'

const quickActions = [
  {
    id: 0,
    name: 'In-App Storage',
    isActive: true,
  },
  {
    id: 1,
    name: 'In-Registration Flow',
    isActive: false,
  },
]

const onClickHandler = (id, sort) => {
  console.log(`clicked: ${id}, ${sort}`)
}

const payload = {
  columns: [
    {
      id: 0,
      label: 'Name',
      isSortable: true,
      sortOrder: SortOrder.ASC,
      onClick: onClickHandler,
    },
    {
      id: 1,
      label: 'Type',
      isSortable: true,
      onClick: onClickHandler,
    },
    {
      id: 2,
      label: 'Description',
      isSortable: false,
      sortOrder: SortOrder.NONE,
      onClick: onClickHandler,
    },
    {
      id: 3,
      label: 'Date Upload',
      isSortable: true,
      sortOrder: SortOrder.NONE,
      onClick: onClickHandler,
    },
  ],
}

const grid = '4fr 2fr 4fr 4fr'

const Component = () => {
  const [actions, setActions] = useState(quickActions)

  const selectActionHandler = id => {
    const a = [...actions].map(action => {
      action.isActive = action.id === id
      return action
    })
    setActions(a)
  }

  return (
    <PageLayout
      header={
        <Filter
          actionName='Add Source'
          placeholder='Enter Location'
          quickAction={actions}
          onSelectAction={selectActionHandler}
          title='Documents'
        />
      }>
      <TableHeader model={payload.columns} gridTemplateColumns={grid} />
      Documents!!!
    </PageLayout>
  )
}

export default Component
