import React from 'react'
import colors from 'styles/colors'
import PropTypes from 'prop-types'

const styles = {
  main: {
    backgroundColor: colors.greyBg,
    fontWeight: 'normal',
    borderRadius: 11,
    borderWidth: 0,
    outline: 0,

  },
  icon: {
    position: 'absolute',
    marginLeft: 15,
    marginTop: 11,
  },
}

const AtmInput = ({ id, style, type, leftIcon, ...props }) => {
  return (
    <>
      {leftIcon && <div style={styles.icon}>{leftIcon}</div>}
      <input
        {...props}
        id={id}
        type={type}
        style={{ ...AtmInput.defaultProps.style, ...styles.main, ...style }}
      />
    </>
  )
}

AtmInput.propTypes = {
  id: PropTypes.string.isRequired,
  style: PropTypes.object,
  type: PropTypes.string,
}

AtmInput.defaultProps = {
  style: {
    width: 328,
    height: 48,
    paddingLeft: 20,
    paddingRight: 20,
  },
  type: 'text',
}

export default AtmInput
