import React from 'react'
import PropTypes from 'prop-types'
import SideMenuItem from './sideMenu/SideMenuItem'
import initialItems from './sideMenu/side-items'

import {
  Schedule,
  Equipment,
  AlertsSide,
  Contacts,
  Document,
} from './sideMenu/icons'
import colors from 'styles/colors'
import { Link } from 'react-router-dom'
import borders from 'styles/borders'

const styles = {
  list: {
    listStyleType: 'none',
    margin: 0,
    padding: 0,
    borderRight: borders.menu,
  },
}

const getIcon = (isActive, svgName) => {
  const fillColor = !!isActive ? colors.red : colors.greyDark
  switch (svgName) {
    case 'schedule': {
      return <Schedule fill={fillColor} />
    }
    case 'equipment': {
      return <Equipment fill={fillColor} />
    }
    case 'contacts': {
      return <Contacts fill={fillColor} />
    }
    case 'document': {
      return <Document fill={fillColor} />
    }
    default: {
      return <AlertsSide fill={fillColor} />
    }
  }
}

const SideMenu = ({ route }) => {
  return (
    <ul style={styles.list}>
      {[...initialItems].map(x => {
        const isActive = x.route === route
        return (
          <li key={x.id}>
            <Link to={x.route}>
              <SideMenuItem name={x.name} isActive={isActive} icon={x.svg}>
                {getIcon(isActive, x.svg)}
              </SideMenuItem>
            </Link>
          </li>
        )
      })}
    </ul>
  )
}

SideMenu.propTypes = {
  route: PropTypes.string.isRequired,
}

export default SideMenu
