import React, { useState } from 'react'
import PropTypes from 'prop-types'
import TableHeader from 'app/table/TableHeader'
import TableRow from 'app/table/TableRow'

const styles = {
  table: {
    overflowY: 'scroll',
    // 100 - header - title - tableHeader
    maxHeight: 'calc(100vh - 80px - 160px - 53px)',
  },
  row: {
    margin: '8px 32px',
  },
}

const Table = ({ payload, grid, menuItems, children }) => {
  const [items, setItems] = useState(payload.items)

  const handleTableClick = () => {
    const its = [...items]
    its.forEach(i => {
      console.log('handle table click')
      if (!!i.showMenu) {
        console.log('item change', i)
        i.showMenu = false
      }
    })

    setItems(its)
    console.log('table click!')
  }

  const onMenuItemSelected = item => {
    console.log('processing menu item', item)
  }

  const iconMoreClicked = id => {
    console.log('processing icon more click')
    const its = [...items]
    its.forEach(i => {
      if (i.id === id) {
        i.showMenu = true
      } else if (!!i.showMenu) {
        i.showMenu = false
      }
    })
    setItems(its)
  }

  return (
    <div onClick={handleTableClick}>
      <TableHeader model={payload.columns} gridTemplateColumns={grid} />

      <div style={styles.table}>
        {items.map(item => {
          return (
            <div style={styles.row} key={item.id}>
              <TableRow
                id={item.id}
                gridTemplateColumns={grid}
                menuItems={menuItems}
                showMenu={item.showMenu}
                menuItemSelected={onMenuItemSelected}
                iconMoreClicked={iconMoreClicked}>
                {children(item)}
              </TableRow>
            </div>
          )
        })}
      </div>
    </div>
  )
}

Table.propTypes = {
  payload: PropTypes.object.isRequired,
  menuItems: PropTypes.array.isRequired,
  grid: PropTypes.string.isRequired,
  children: PropTypes.func.isRequired,
}

export default Table
