import React from 'react'
import { Header as Hdr, Box } from 'grommet'
import colors from 'styles/colors'
import borders from 'styles/borders'
import Logo from './header/Logo'
import PropTypes from 'prop-types'
import Search from './header/Search'
import Alerts from './header/Alerts'
import Profile from './header/Profile'

const styles = {
  main: {
    width: '100%',
    height: '100%',
  },
  img: {
    borderLeft: borders.menu,
    height: 80,
    width: 80,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    cursor: 'pointer',
  },
}

const Header = ({ style, children, isAuth, ...props }) => {
  return (
    <div style={styles.main}>
      <Hdr
        style={{ ...style, height: '100%', paddingRight: 0 }}
        {...props}
        background={colors.transparent}
        pad='small'>
        <div className='m-l-32'>
          <Logo />
        </div>
        {isAuth && (
          <Box direction='row'>
            <Box style={styles.img}>
              <Search />
            </Box>
            <Box style={styles.img}>
              <Alerts />
            </Box>
            <Box style={styles.img}>
              <Profile />
            </Box>
          </Box>
        )}
      </Hdr>
    </div>
  )
}

Header.propTypes = {
  children: PropTypes.node,
  style: PropTypes.object,
  isAuth: PropTypes.bool,
}

export default Header
