import React from 'react'
import PropTypes from 'prop-types'
import colors from 'styles/colors'

const styles = {
  id: {
    borderRadius: '50%',
    minWidth: 32,
    minHeight: 32,
    backgroundColor: colors.red,
    color: colors.white,
  },
  name: {
    maxHeight: 48,
    fontWeight: 500,
  },
  type: {
    color: colors.greyDark,
    fontWeight: 500,
    lineHeight: '16px',
  },
}

const Tool = ({ id, name, type }) => {
  return (
    <div className="center-vertical" style={styles.main}>
      <div className='center' style={styles.id}>
        {id}
      </div>
      <div className="m-l-12">
        <div className='f-s-16 text-ellipsis' style={styles.name}>
          {name}
        </div>
        <div style={styles.type}>{type.name}</div>
      </div>
    </div>
  )
}

Tool.propTypes = {
  id: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  type: PropTypes.shape({
    id: PropTypes.number.isRequired,
    name: PropTypes.string.isRequired,
  }),
}

export default Tool
